.header-color {
  background: #3494E6;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, rgb(88, 0, 139), rgb(211, 117, 255));  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, rgb(88, 0, 139), rgb(211, 117, 255)); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.landing-grid {
  background: linear-gradient(to right, rgb(88, 0, 139), rgb(211, 117, 255));
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

}

.avatar-img{
  height: 350px;
  padding-top: 30%;
  padding-left: 40%;
}

.banner-text{
  padding-top: 15%;
  
  opacity: .8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}

.banner-text h1{
  font-size: 66px;
  font-weight: bold;
  color: white;
  
}

.banner-text h2{
  font-size: 62px;
  font-weight: bold;
  color: white;
}

.banner-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.redes-sociais{
  display: flex;
  justify-content: center;
  width: 40%;
  margin: auto;


}

.redes-sociais i{
  color: white;
  font-size: 5em;
  padding: 10px;

}

.projects-grid{
  display: flex;
}

.contato-body{
  margin: auto;
  background: linear-gradient(to right, rgb(88, 0, 139), rgb(211, 117, 255));
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}

.contato-grid{
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 550px;
  background: white;
  color: black;
  border-radius: 2%;
  margin-bottom: 20px;
}

.contato-grid h2{
  font-family: 'Alata', sans-serif;
}

.contato-grid p{
  font-family: 'Alata', sans-serif;
  font-weight: bold;
}

.contato-list i {
  font-size: 50px;
  padding-right: 5rem;
}

.resume-coluna-esquerda{
  
  color: white;
}

.resume-coluna-direita{
  background: white;
  color: black;
}

.resume-grid{
  background: linear-gradient(to right, rgb(88, 0, 139), rgb(211, 117, 255));
}

.grid-galeria{
  position: relative;
  margin-left: 20px;
}